}
.buy-button span {
  transition: scale 200ms ease-in-out;
}
.buy-button:disabled {
  background-color: #DFE0E1;
}
.buy-button:disabled span {
  scale: 0;
}
.buy-button:disabled .loader-container {
  scale: 1;
}
.buy-button .loader-container {
  transition: scale 200ms ease-in-out;
  scale: 0;
}
.buy-button .loader {
